.body,
body {
  background-color: #684d9b;
}
.body {
  height: 100vh;
}
header {
  font-family: "Itim";
  font-size: 45px;
  font-weight: 700;
  color: #d9cf23;
}
footer,
header {
  background-color: #61488f;
  text-align: center;
}
footer {
  position: static;
  color: #e9e9e9;
  width: 100%;
}
.middle {
  display: -webkit-flex;
  display: flex;
  height: 90vh;
  box-sizing: border-box;
  -webkit-align-items: center;
  align-items: center;
  -webkit-justify-content: center;
  justify-content: center;
  font-size: 25px;
  color: aliceblue;
}
.container {
  height: 70vh;
  width: 70vw;
  font-size: 46px;
  border-radius: 55px;
  border: 10px solid #493869;
  background-color: #493869;
  text-align: center;
}
.answersContainer {
  -webkit-align-items: stretch;
  align-items: stretch;
  font-size: 15px;
  -webkit-flex-wrap: wrap;
  flex-wrap: wrap;
}
.answersContainer,
.answersContainer > div {
  -webkit-justify-content: center;
  justify-content: center;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
  flex-direction: row;
}
.answersContainer > div {
  height: 10vh;
  -webkit-align-items: center;
  align-items: center;
}
.answersContainer > div > div {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
  justify-content: center;
  -webkit-flex-direction: row;
  flex-direction: row;
}
.answer {
  -webkit-flex-basis: calc(50% - 20px);
  flex-basis: calc(50% - 20px);
  font-family: "Itim";
  color: black;
  font-size: 30px;
  width: 50%;
  height: 30%;
  margin: 10px;
  border-radius: 20px;
  background-color: rgb(252, 181, 246);
  transition: 0.25s ease-out;
  display: -webkit-flex;
  display: flex;
}
.answer:hover {
  color: rgb(43, 43, 43);
  background-color: #549bf7;
  cursor: pointer;
  border: 3px solid aliceblue;
}

.answer:active {
  background-color: #2372d9;
}
.quest {
  font-family: "Itim";
  color: azure;
  display: -webkit-flex;
  display: flex;
  font-size: 45px;
  -webkit-flex-direction: column;
  flex-direction: column;
  width: 100%;
  height: 50%;
  -webkit-justify-content: center;
  justify-content: center;
  overflow: hidden;
}
.loader {
  display: flex;
  margin: 0 auto;
  margin-top: 20%;
  border-radius: 50%;
  border: 16px solid #61488f;
  border-top-color: #549bf7;
  width: 250px;
  height: 250px;
  -webkit-animation: spin 2s linear infinite;
  animation: spin 2s linear infinite;
}
@-webkit-keyframes spin {
  0% {
    -webkit-transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(1turn);
  }
}
@keyframes spin {
  0% {
    -webkit-transform: rotate(0deg);
    transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(1turn);
    transform: rotate(1turn);
  }
}
.unselectable {
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -ms-user-select: none;
  user-select: none;
}

.gameEnd {
  display: -webkit-flex;
  display: flex;
  color: #f0f8ff;
  align-items: center;
  margin: 0 auto;
  text-align: center;
  font-family: "Itim";
  font-size: 65px;
  margin-top: 10%;
  background-color: #493869;
  border-radius: 25px;
  width: 40vw;
  height: 30vh;
  -webkit-justify-content: center;
  justify-content: center;
  -webkit-align-items: center;
  border: 3px solid aliceblue;
}

.playAgain {
  transition: 0.35s ease-in-out;
  display: -webkit-flex;
  display: flex;
  color: aliceblue;
  align-items: center;
  margin: 0 auto;
  text-align: center;
  font-family: "Itim";
  font-size: 35px;
  margin-top: 5%;
  background-color: #549bf7;
  border: 2px solid aliceblue;
  cursor: pointer;
  border-radius: 25px;
  width: 20vw;
  height: 13vh;
  -webkit-justify-content: center;
  justify-content: center;
  -webkit-align-items: center;
}

.playAgain:hover {
  background-color: #2372d9;
  transition: 0.35s ease-in-out;
}
@media screen and (max-width: 900px) {
  .container {
    margin-top: 10px;
    height: 80vh;
    width: 90vw;
  }

  .answer {
    font-size: 12px;
  }

  .quest {
    font-size: 22px;
  }

  .answer:hover {
    transition: 0.25s ease-out;
    font-size: 12px;
  }
  .gameEnd {
    margin-top: 20vh;
    font-size: 25px;
    width: 60vw;
    height: 20vh;
  }

  .playAgain {
    font-size: 25px;
    margin-top: 10vh;
    width: 50vw;
    height: 10vh;
  }

  .next {
    width: 50vw !important;
    font-size: 20px !important;
  }
}

@media screen and (max-width: 320px) {
  .container {
    margin-top: 10px;
    height: 80vh;
    width: 90vw;
  }

  .answer {
    font-size: 9px;
  }

  .quest {
    font-size: 18px;
  }

  .answer:hover {
    transition: 0.25s ease-out;
    font-size: 11px;
  }
  .gameEnd {
    font-size: 25px;
    width: 60vw;
    height: 25vh;
  }
  .next {
    cursor: pointer;
    width: 50vw !important;
    font-size: 20px !important;
  }
}
.random {
  background-color: rgb(252, 181, 246);
  border: 3px solid rgb(252, 181, 246);
}
.random:hover {
  background-color: rgb(252, 181, 246);
  border: 3px solid rgb(252, 181, 246);
}
random:active {
  background-color: rgb(252, 181, 246);
  border: 3px solid rgb(252, 181, 246);
}

.clicked {
  border: 3px solid aliceblue;
  background-color: #c72923;
  color: aliceblue;
}

.clicked:hover {
  border: 3px solid aliceblue;
  background-color: #c72923;
  color: aliceblue;
}
.correct {
  background-color: #549b54d8;
  color: aliceblue;
}

.correct:hover {
  background-color: #549b54d8;
  color: aliceblue;
}
.correct:active {
  background-color: #549b54d8;
  color: aliceblue;
}

.wrong {
  background-color: #c72923;
  color: aliceblue;
}
.wrong:hover {
  background-color: #c72923;
  color: aliceblue;
}
.wrong:active {
  background-color: #c72923;
  color: aliceblue;
}

.next {
  margin-top: 2.3vh;
  margin-bottom: 3vh;
  font-family: "Itim";
  color: aliceblue;
  font-size: 20px;
  border-radius: 15px;
  border: 2px solid aliceblue;
  width: 11vw;
  height: 5.5vh !important;
  background-color: #2372d9;
  cursor: pointer;
}

.flex-body {
  display: flex;
  min-height: 100vh;
  flex-direction: column;
  justify-content: space-between;
}
